  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d60041', endColorstr='#a90040',GradientType=0 );
  cursor: pointer;
}

.order-now, .order-now:hover {
  transition: all .3s;
  -moz-transition: all .3s;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
}


/*footer section*/
#footer{
  position: relative;
  border-top: 1px solid #535353;
  font-size: 12px;
  padding-bottom: 60px;
  color: #999;
}

#footer p {
  margin: 10px 0;
}


#footer a {
  color: #d60041;
  font-size: 12px;
}

/*clear floats*/
#header:after,
#shop-list:after,
#header:after,
#shop-info:after,
#cartList:after,
#shopping-cart:after,
#main-section:after,
#footer:after,
table tbody td:after{
  content:"";
  clear:both;
  display: block;
}

.cart-image-wrapper {
  display: inline-block;
  float: left;
  height: 100px;
  overflow: hidden;
}

/* for offline demo */
.demo-section {
  color: #bababa;
  width: 50%;
}

